body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}
.form-section {
  padding: 20px;
}
.textarea-section {
  padding-top: 20px;
  position: relative;
}
.textarea-section textarea {
  resize: none;
  height: 600px;
}
.CodeMirror {
  border: 1px solid #eee;
  height: 500px;
  font-size: 0.7em;
}
.preview-section {
  padding-top: 20px;
  padding-right: 20px;
  position: relative;
}
.preview-section button {
  margin-bottom: 18px;
  margin-right: 5px;
}
#preview {
  width: 100%;
  border: 1px solid #eee;
  min-height: 500px;
}
@media only screen and (min-width : 992px) {
  .fix-content {
    position: fixed;
    right: 0;
  }
}